<template>
  <div class="school-year">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-tabs v-model="tabs" @tab-click="handleClick">
        <el-tab-pane label="年级总课表" name="grade"></el-tab-pane>
        <el-tab-pane label="老师单课表" name="teacher"></el-tab-pane>
        <el-tab-pane label="班级单课表" name="class"></el-tab-pane>
      </el-tabs>
      <transition-group mode="out-in" name="el-fade-in-linear">
        <schedule-grade key="1" v-if="tabs === 'grade'"></schedule-grade>
        <schedule-teacher key="2" v-if="tabs === 'teacher'"></schedule-teacher>
        <schedule-class key="3" v-if="tabs === 'class'"></schedule-class>
      </transition-group>
    </main-part>
  </div>
</template>
<script>
  import scheduleGrade from './ScheduleGrade'
  import scheduleTeacher from './ScheduleTeacher'
  import scheduleClass from './ScheduleClass'
  export default {
    components:{
      scheduleGrade,
      scheduleTeacher,
      scheduleClass
    },
    data() {
      return {
        nav: [{name: "课表管理", path: ""}],
        tabs: this.$route.query.type || 'grade',
      }
    },
    methods: {
      handleClick() {
        this.$router.push({
          path: this.$route.path,
          query:{
            type: this.tabs
          }
        })
      }
    },
    mounted() {
    }
  }
</script>
