<template>
  <div class="index">
    <div class="btn-oper">
      <div @click="showDialog" class="btn-main">导入课表</div>
    </div>
    <div class="search-box">
      <div class="item">
        <span class="text">学期</span>
        <el-select @change="getTimeTable()" class="term-input" v-model="form.termId">
          <el-option v-for="item in termList" :key="item.schoolTermId"
                     :label="item.termName" :value="item.schoolTermId"
          ></el-option>
        </el-select>
      </div>
      <div class="item">
        <span class="text">年级</span>
        <el-select @change="getTimeTable()" v-model="form.gradeId">
          <el-option v-for="item in gradeList" :key="item.id"
                     :label="item.name" :value="item.id"
          ></el-option>
        </el-select>
      </div>
    </div>
    <div class="table-box mt20">
      <div v-show="tableData" v-html="tableData"></div>
      <div class="no-data" v-show="!tableData">暂无数据</div>
    </div>
    <el-dialog title="导入课表" class="dialog" :visible.sync="dialog" width="540px">
      <div class="item">
        <span class="title imp">学期</span>
        <el-select v-model="impTermId" class="input">
          <el-option v-for="item in termList" :key="item.schoolTermId"
                     :label="item.termName" :value="item.schoolTermId"
          ></el-option>
        </el-select>
      </div>
      <div class="item">
        <span class="title imp">上传文件</span>
        <el-input type="file" class="input file-input" ref="upload"
                  accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                  placeholder="请选择"></el-input>
      </div>
      <div class="btns">
        <div @click="impFun()" class="btn-submit btn-upload mr15">确认</div>
        <div @click="dialog = false" class="btn-clear">取消</div>
      </div>
    </el-dialog>
  </div>

</template>
<script>
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$spliceHTML],
    data() {
      return {
        // 学期列表
        termList: [],
        // 年级列表
        gradeList: [],
        // 查询条件
        form:{
          termId: null,
          gradeId: null,
          timetableType: 'GRADE'
        },
        //
        tableData: '',
        dialog: false,
        // 导入时选择的学期
        impTermId: null,
      }
    },
    methods: {
      // 展现弹窗
      showDialog(){
        this.dialog = true
      },
      // 获取学期
      async getTermList(){
        this.termList = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.termList.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.impTermId = this.form.termId = e.schoolTermId
          }
        })
      },
      // 获取年级
      async getGradeList(){
        this.gradeList = await this.$ajax({
          url: '/grade/queryGradeList.do'
        })
        this.form.gradeId = this.gradeList[0].id
      },
      // 获取课表
      async getTimeTable(){
        let res = await this.$ajax({
          url: '/timetable/queryTimetableHtml.do',
          el: '.table-box',
          query: this.form
        })
        if(res.rspCd){
          this.tableData = false
        }else{
          this.tableData = this.$spliceHTML(res)
        }
      },
      // 导入课表
      async impFun() {
        if(!this.impTermId){
          this.$message.warning('请选择学期')
          return
        }
        let file = this.$refs.upload.$refs.input.files[0]
        if(!file){
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('termId', this.impTermId)
        let obj = {
          url: `/import/importTimetable.do?termId=${this.impTermId}`,
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.$refs.upload.value = null
        this.$remind('导入成功')
        this.dialog = false
        this.$search(this.table)
      },
    },
    async mounted(){
      await this.getTermList()
      await this.getGradeList()
      await this.getTimeTable()
    }
  }
</script>
