<template>
  <div class="index">
    <div class="search-box">
      <div class="item">
        <span class="text">学期</span>
        <el-select @change="getTimeTable()" class="term-input" v-model="form.termId">
          <el-option v-for="item in termList" :key="item.schoolTermId"
                     :label="item.termName" :value="item.schoolTermId"
          ></el-option>
        </el-select>
      </div>
      <div class="item">
        <span class="text">班级</span>
        <el-select
          @change="getTimeTable()"
          v-model="form.classId"
          class="input"
          filterable
          default-first-option
          remote
          clearable
          reserve-keyword
          placeholder="班级名称"
          :remote-method="getClassList"
          :loading="loading">
          <el-option
            v-for="item in classList"
            :key="item.id"
            :label="item.aliasName"
            :value="item.classId">
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="table-box mt20">
      <div v-show="tableData" v-html="tableData"></div>
      <div class="no-data" v-show="!tableData">暂无数据</div>
    </div>
  </div>

</template>
<script>
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$spliceHTML],
    data() {
      return {
        // 学期列表
        termList: [],
        // 教师列表
        classList: [],
        // 查询条件
        form:{
          termId: null,
          classId: null,
          timetableType: 'CLASS'
        },
        //
        tableData: '',
        dialog: false,
        loading: false
      }
    },
    methods: {
      // 获取学期
      async getTermList(){
        this.termList = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.termList.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.form.termId = e.schoolTermId
          }
        })
      },
      // 获取班级
      async getClassList(query){
        if(query !== ''){
          this.loading = true
          let res = await this.$ajax({
            url: '/class/queryClassPage.do',
            query:{
              keywords: query,
              pageNo: 1,
              pageSize: 15
            }
          })
          this.loading = false

          this.classList = res.list
        }else{
          this.classList = []
        }
      },
      // 获取课表
      async getTimeTable(){
        let res = await this.$ajax({
          url: '/timetable/queryTimetableHtml.do',
          el: '.table-box',
          query: this.form
        })
        if(res.rspCd){
          this.tableData = false
        }else{
          this.tableData = this.$spliceHTML(res)
        }
      },
    },
    async mounted(){
      await this.getTermList()
      await this.getClassList()
      this.form.classId = this.classList[0].classId
      await this.getTimeTable()
    }
  }
</script>
